import { html } from 'lit';
import { ifDefined } from 'lit-html/directives/if-defined.js';

export type TMarketColorSwatchListTemplateArgs = Partial<HTMLMarketColorSwatchListElement> & {
  customSwatches?: string;
};

export const ColorSwatchListTemplate = ({ customSwatches, value }: TMarketColorSwatchListTemplateArgs) => html`
  <market-color-swatch-list value=${ifDefined(value)}
    >${customSwatches
      ? customSwatches.split(',').map((color) => html`<market-color-swatch value=${color}></market-color-swatch>`)
      : ''}</market-color-swatch-list
  >
`;
